.expirience-section {
	font-family: $font-family-SFPro;
	font-size: 14px;
	font-weight: 400;
	line-height: 130%;
	height: 100%;
	h2 {
		font-size: 20px;
		line-height: 135%;
		font-weight: 700;
		margin-bottom: 5.33vw;
	}
	.expirience-item {
		margin-bottom: 16vw;
		opacity: 0;
		transition: opacity 2s;
		
		&__date  {
			color: rgba(130, 130, 130, 1);
			padding-bottom: 2.1vw;
		}
	
		&__title {
			font-weight: 700;
			color: rgba(40, 42, 51, 1);
			padding-bottom: 2.1vw;
		}
		&__company {
			color: rgba(40, 42, 51, 1);
			padding-bottom: 2.1vw;
			display: flex;
			flex-direction: row;
			align-items: center;
			justify-content: flex-start;
			
			img {
				width: 8vw;
				height: 8vw;
				border-radius: 50%;
				margin-right: 2.1vw;
			}

			&__title {
				&__name {
					font-weight: 700;
					color: rgba(40, 42, 51, 1);
				}
	
				&__location {
					font-weight: 400;
					color: rgba(40, 42, 51, 1);
				}
			}
		}
		&__description {
			color: rgba(40, 42, 51, 1);
			padding-bottom: 2.1vw;
		}
	}

	@include tablet {
		font-size: 18px;

		h2 {
			font-size: 24px;
		}
	}

	@include desktop {
		width: 49.5%;
		font-size: 14px;
		font-weight: 400;
		line-height: 130%;
		margin-bottom: 13.8vw;
		h2 {
			font-size: 24px;
			margin-bottom: 2.1vw;
		}
		.expirience-item {
			margin-bottom: 4.1vw;
			opacity: 0;
			transition: opacity 2s;
			
			&__date  {
				color: rgba(130, 130, 130, 1);
				padding-bottom: .55vw;
			}
		
			&__title {
				padding-bottom: .55vw;
			}
			&__company {
				padding-bottom: .55vw;
				
				img {
					width: 2.1vw;
					height: 2.1vw;
					border-radius: 50%;
					margin-right: .55vw;
				}
			}
		}
	}
}